.search {
  width: 100%;
  height: 40px;
  overflow: hidden;
  background-color: #e8e8e8;
}

.search_box {
  width: 90%;
  height: 32px;
  margin:4px auto;
  background-color: #fff;
  border-radius: 16px;
  overflow: hidden;
  position: relative;
}

.key_word {
  width: 80%;
  height: 30px;
  margin-top: 1px;
  margin-left: 4px;
  display: block;
  line-height: 32px;
  outline:none;
  border: none;
  font-size: 14px;
  color: #6f6f6f;
}

.search_btn {
 position: absolute;
 right: 0;
 top:0;
 background-size: 28px 28px;
 background-position: center;
 width: 32px;
 height: 32px;
 border-radius: 20px;
}

.nav {
  width: 100%;
  height: 40px;
}

.tab {
  width: 60px;
  height: 40px;
  float: left;
  background-color: #fff;
  text-align: center;
  line-height: 40px;
  color: #6f6f6f;
  font-size: 14px;
}

.tab:hover {
  background-color: #e4e4e4;
}

.goods {
  width: 100%;
  height: 100px;
}

.goods_img {
  width: 100px;
  height: 100px;
  float: left;
  margin: 0;
}

.goods_info {
  width: calc(100% - 100px);
  height: 100px;
  float: left;
}

.goods_name {
  padding-left: 10%;
  width: 90%;
  height: 40px;
  line-height: 20px;
  margin: 0;
  padding: 0;
  font-size: 24px;
  color: #6f6f6f;
}

.goods_price {
  height: 60px;
  line-height: 60px;
  margin: 0;
  font-size: 20px;
  color:#fe0d0d;
  text-align: right;
  width: 90%;
  padding-right: 10%;
}

.goods_list {
  width: 100%;
}

.more_goods {
  width: 100%;
  height: 40px;
  text-align: center;
  line-height: 40px;
  background-color: #f2f2f2;
}



